<template>
  <div class="page-view" append="tree">
    <div class="page-header header page-header-background page-header-text">
      <div class="cancel" @click="cancelNewTodoHandler"> </div>
      <div class="save row-padding-10" @click="saveNewTodoHandler"> </div>
    </div>
    <div class="add-todo-view-content row">
      <form class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input placeholder="" id="todoTitle" type="text" class="validate" v-model="todoTitle">
            <label for="todoTitle"></label>
          </div>
          <div class="input-field col s12">
            <textarea id="comment" class="materialize-textarea" v-model="todoDesc"></textarea>
            <label for="comment"></label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <select v-select="priority">
              <option value="" disabled></option>
              <template v-for="star in stars">
                <option v-if="star == priority" :value="star" selected> {{ star }}</option>
                <option v-else :value="star"> {{ star }}</option>
              </template>
            </select>
            <label></label>
          </div>
        </div>
        <div class="row">
          <DatePicker class="col s6" title="" v-on:updateSelectedDate="setExpectedFinishDate"></DatePicker>
          <!--   -->
          <TimePicker class="col s6" title="" :propDate='expectFinishDate' v-on:updateSelectedTime="setExpectFinishTime"></TimePicker>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <select v-select="projectId">
              <option value="" disabled selected></option>
              <option v-for="item in activeProjects" :value="item.projectId">{{ item.projectName }}</option>
              <!--<option value="project001">EDA</option>-->
              <!--<option value="project002"></option>-->
              <!--<option value="project003">todo</option>-->
            </select>
            <label></label>
          </div>
        </div>
        <!---->
        <!--<div class="row">-->
          <!--<div class="input-field col s12">-->
            <!--<select v-select="sceneId">-->
              <!--<option value="" disabled selected></option>-->
              <!--<option v-for="item in activeScenes" :value="item.sceneId"> {{ item.sceneName }}</option>-->
            <!--</select>-->
            <!--<label></label>-->
          <!--</div>-->
        <!--</div>-->
        <div class="row">
          <div class="input-field col s12">
            <div class="chips chips-placeholder">
              <input id="tags" class="input" placeholder="">
              <!--<label for="tags"></label>-->
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
  // CSS
  require('../assets/css/common.css')
  import $ from 'jquery'
  import TimePicker from './common/time-picker'
  import DatePicker from './common/date-picker'

  export default {
    components: { TimePicker, DatePicker },
    data: function () {
      return {
        stars: ['1', '2', '3', '4', '5'],
        todoTitle: '',
        todoId: '',
        todoDesc: '',
        priority: '',
        expectFinishDate: '',
        expectFinishTime: '',
//        sceneId: '',
        projectId: '',
        tags: [],
        spentClock: 0,
        isFinished: false
      }
    },
    computed: {
      // 
      newTODOItem () {
        return {
          'todoTitle': this.todoTitle,
          'todoId': this.todoId,
          'todoDesc': this.todoDesc,
          'priority': this.priority,
          'expectFinishDate': this.expectFinishDate,
          'expectFinishTime': this.expectFinishTime,
//          'sceneId': this.sceneId,
          'projectId': this.projectId,
          'tags': this.tags,
          'spentClock': this.spentClock,
          'isFinished': this.isFinished
        }
      },
      // project
      activeProjects () {
        return this.$store.getters.activeProjects
      },
      // scenes
//      activeScenes () {
//        return this.$store.getters.activeScenes
//      }
    },
    beforeMount: function () {
      this.$store.dispatch('LOAD_PROJECTS')
//      this.$store.dispatch('LOAD_SCENES')
    },
    mounted: function () {
      var VM = this
      // materialize-css 
      $('select').material_select()
      $('.chips').material_chip()
      $('.chips-placeholder').material_chip({
        placeholder: '',
        secondaryPlaceholder: ''
      })
      $('.chips').on('chip.add', function (e, chip) {
        // you have the added chip here
        VM.tags.push({'tagName': chip.tag})
      })
      $('.chips').on('chip.delete', function (e, chip) {
        // you have the added chip here
        VM.tags = VM.deleteObjectInArray(VM.tags, 'tagName', chip.tag)
      })
    },
    methods: {
      // tools: delete ele in array
      deleteElementInArray (obj, target) {
        var index = obj.indexOf(target)
        if (index > -1) {
          return obj.slice(0, index).concat(obj.slice(index + 1))
        } else {
          return obj
        }
      },
      // tools: delete ele in Object
      deleteObjectInArray (obj, propNm, target) {
        for (var p in obj) {
          if (obj[p].hasOwnProperty(propNm) && obj[p][propNm] === target) {
            obj.splice(p, 1)
            break
          }
        }
        return obj
      },
      // 
      cancelNewTodoHandler (event) {
        console.log('cancel and go back!')
        this.newTODOItem = {}
        this.$router.back()
      },
      // 
      saveNewTodoHandler (event) {
        console.log('save the new todo task')
        // 
        this.$store.dispatch('ADD_TODO', {'item': this.newTODOItem})
        this.$router.back()
      },
      // 
      setExpectFinishTime (event) {
        console.log('set the setExpectFinishTime')
        this.expectFinishTime = event || '23:59'
      },
      // 
      setExpectedFinishDate (event) {
        console.log('set the setExpectedFinishDate')
        this.expectFinishDate = event
      },
      updateValue (event) {
        console.log('date')
      }
    }
  }
</script>

<style scoped>
  .add-todo-view {
    height: 100%;
  }

  .add-todo-view .row .col {
    padding: 0px
  }

  .add-todo-view .row div {
    padding: 0 1rem;
  }

  .select-wrapper .caret {
    right: 5px !important;
  }

  .add-todo-view-header {
    position: fixed;
    top: 0;
    font-size: 16px;
    width: 100%;
    height: 80px;
    z-index: 2;
    margin-bottom: 3px;
    display: flex;
    flex-flow: nowrap;
    flex-direction: row;
    justify-content: flex-start;
    -webkit-justify-content: flex-start;
    align-items: center;
    -webkit-align-items: center;
    border-bottom-width: 2px;
    border-bottom-style: solid;
    border-bottom-color: #35495e;
  }

  .add-todo-view-content {
    margin-top: 80px;
  }
  .add-todo-view .cancel {
    flex: 0 0 70%;
    padding-left: 15px;
  }

  .row-padding-5 {
    padding: 0 5px;
  }
</style>



